.navbar{background:#FFC888;height:110px;box-shadow:0px 8px 33px -5px rgba(34,60,80,0.15)}.navbar__wrapper{display:flex;margin:0 126px;align-items:center;height:100%;justify-content:space-between}.navbar__logo{align-self:center;font-style:normal;font-weight:bold;font-size:28px;line-height:34px;color:#f90}.navbar__list{display:flex}.navbar__search-field,.navbar__search-btn{border:1px solid #606369;font-family:Montserrat;font-style:normal;font-weight:400;font-size:20px;color:#606369;height:30px}.navbar__search-field{width:400px;border-top-left-radius:5px;border-bottom-left-radius:5px;padding-left:10px;border-right:none}.navbar__search-btn{background:#fff;border-top-right-radius:5px;border-bottom-right-radius:5px}.navbar__item-search{width:28px;height:28px}.navbar__item-search-ico{cursor:pointer}.navbar__item-search-ico:hover{opacity:.6}.navbar__item-link{text-decoration:none;font-style:normal;font-weight:500;font-size:22px;line-height:27px;color:#606369;margin-left:52px}.navbar__item-link_active{font-weight:bold}.navbar__item-link:hover{opacity:.6}

